/**
 * Dialog
 * A reuseable modal
 */

var Focus = require('../../mixins/focus')
var React = require('react')

var Types = React.PropTypes

var Dialog = React.createClass({displayName: 'Dialog',

  mixins: [ Focus ],

  propTypes: {
    onExit : Types.func.isRequired,
  },

  render:function() {
    return (
      React.createElement("div", {className: "ars-dialog", tabIndex: "0", role: "dialog", onKeyUp:  this._onKeyUp}, 

        React.createElement("div", {className: "ars-dialog-blackout", 'aria-hidden': true, onClick:  this.props.onExit}), 

        React.createElement("section", {className: "ars-dialog-inner"}, 
           this.props.children
        )

      )
    )
  },

  _onKeyUp:function(e) {
    if (e.key === 'Escape') {
      this.props.onExit()
    }
  }

})

module.exports = Dialog



