/**
 * Photo
 * The resource responsible for fetching and managing operations related to
 * the API endpoint for images.
 */

var xhr = require('xhr')

var Photo = {

  fetch:function(url, success, error) {
    var request = xhr({ url:url, json: true }, function(err, response, body) {
      err ? error(err) : success(body)
    })

    return request
  },

  filter:function(items, query) {
    var pattern = new RegExp(query, 'i')

    return query ? items.filter(function(i)  {return i.caption.match(pattern);}) : items
  },

  find:function(items, id) {
    if (id == void 0) return null

    return items.find(function(i)  {return i.id.toString() === id.toString();})
  },

  datalist:function(items) {
    return items.map(function(i)  {return i.caption;})
  }

}

module.exports = Photo



